import { useState } from 'react';
import axios from 'axios';
import Header from '../../../components/Header';
import styles from './CheckDocumentNumber.module.css'; //   CSS

export default function CheckDocumentNumber() {
  const [documentNumber, setDocumentNumber] = useState('');
  const [documentData, setDocumentData] = useState(null);
  const [error, setError] = useState(null);
  const [loading, setLoading] = useState(false);

  //        
  const checkDocument = async () => {
    if (!documentNumber) {
      setError('  ');
      return;
    }

    setLoading(true);
    setError(null);
    try {
      const response = await axios.get('/api/check-document-number', {
        params: { document_number: documentNumber },
        headers: {
          Authorization: `Bearer ${localStorage.getItem('token')}`,
        },
      });

      if (response.data.exists) {
        setDocumentData(response.data); //   
      } else {
        setError('  ');
        setDocumentData(null); //    ,  
      }
    } catch (err) {
      setError('   ');
      setDocumentData(null);
    } finally {
      setLoading(false);
    }
  };

  return (
    <Header>
      <div className={styles.container}>
        <p className={styles.title}>  </p>
        <p className={styles.infoText}>
            ,      .
          ,      .
        </p>

        <input
          type="text"
          value={documentNumber}
          onChange={(e) => setDocumentNumber(e.target.value)}
          placeholder="  "
          className={styles.inputField}
        />
        <button onClick={checkDocument} disabled={loading} className={styles.button}>
          {loading ? '...' : ''}
        </button>

        {error && <p className={styles.errorMessage}>{error}</p>}

        {documentData && (
        <div className={styles.documentInfoContainer}>
          <h3 className={styles.documentTitle}>  :</h3>
          <p> : <span className={styles.commonSpan}>#{documentData.document_number}</span></p>
          <p>: <span className={styles.commonSpan}>{documentData.score || ' '}  5</span></p>
          <p>: <span className={styles.commonSpan}>{documentData.class || ' '}</span></p>
          <p>: <span className={styles.commonSpan}>{documentData.subject || ' '}</span></p>
          <p>: <span className={styles.commonSpan}>{documentData.date || ' '}</span></p>
          <h3 className={styles.documentTitle}> :</h3>
          <p>: <span className={styles.commonSpan}>{documentData.user.firstname}</span></p>
          <p>: <span className={styles.commonSpan}>{documentData.user.lastname}</span></p>
          {
            documentData.user.thirdname && (
              <p><span className={styles.fieldLabel}>:</span> {documentData.user.thirdname}</p>
            )
          }
          <div className={styles.backLogo}>
            <p className={styles.backLogoText}>EruditGroup</p>
          </div>
        </div>
      )}

      </div>
    </Header>
  );
}
