import { useState, useEffect } from "react";
import axios from "axios";
import { Link, useNavigate } from "react-router-dom";
import styles from './Login.module.css';
import Header from "../../../components/Header";

export default function Login() {
  const [data, setData] = useState({ username: "", password: "" });
  const [error, setError] = useState(null);
  const [successMessage, setSuccessMessage] = useState(null);
  const [loading, setLoading] = useState(false);
  const navigate = useNavigate();

  //      
  const handleChange = (e) => {
    const { name, value } = e.target;
    setData({
      ...data,
      [name]: value,
    });

    // Clear error message when the user starts typing
    if (error) {
      setError(null);
    }
  };

  //   
  const handleSubmit = async (e) => {
    e.preventDefault();
    setError(null); //    
    setSuccessMessage(null); //    
    setLoading(true); //   

    try {
      const response = await axios.post("/api/login", data);

      if (response.status === 200 && response.data.token) {
        const token = response.data.token;
        localStorage.setItem("token", token);  //    localStorage
        setSuccessMessage("  !");  //    
      } else {
        setError("   .");
      }
    } catch (error) {
      if (error.response) {
        if (error.response.status === 401) {
          setError("   .");
        } else {
          setError("   .");
        }
      } else {
        setError("   .");
      }
    } finally {
      setLoading(false); //   
    }
  };

  useEffect(() => {
    if (successMessage) {
      navigate('/profile');
    }
  }, [successMessage, navigate]);


  return (
    <>
      <Header />
      <div className={styles.loginForm}>
        <p className={styles.loginForm_title}>  </p>

        {/*     */}
        {error && <div className={styles.errorMessage}>{error}</div>}

        {/*     */}
        {successMessage && <div className={styles.successMessage}>{successMessage}</div>}

        <form onSubmit={handleSubmit}>
          <div className={styles.inputGroup}>
            <input
              type="text"
              name="username"
              value={data.username}
              onChange={handleChange}
              placeholder=": "
              required
            />
          </div>
          <div className={styles.inputGroup}>
            <input
              type="password"
              name="password"
              value={data.password}
              onChange={handleChange}
              placeholder=": "
              required
            />
          </div>
          <div>
            <button type="submit" className={styles.submitButton} disabled={loading}>
              {loading ? '...' : ''}
            </button>
          </div>
          <div className={styles.back}>
            <Link className={styles.backNav} to={"/"}> </Link>
          </div>
          <div className={styles.reg_nav}>
            <p className={styles.reg_nav_text}> ? <Link to={'/auth/register'}></Link></p>
          </div>
        </form>
      </div>

    </>
  );
}
