import { useState } from "react";
import axios from "axios";
import { useNavigate, Link } from "react-router-dom";
import styles from './Login.module.css';  //   CSS
import Header from "../../../components/Header";

export default function AdminLogin() {
  const [data, setData] = useState({ username: "", password: "" });
  const [error, setError] = useState(null);
  const [successMessage, setSuccessMessage] = useState(null);
  const navigate = useNavigate();

  //      
  const handleChange = (e) => {
    const { name, value } = e.target;
    setData({
      ...data,
      [name]: value,
    });
  };

  //   
  const handleSubmit = async (e) => {
    e.preventDefault();
    setError(null); //    
    setSuccessMessage(null); //    

    try {
      const response = await axios.post("/api/admin/login", data);

      if (response.status === 200 && response.data.token_admin) {
        const token = response.data.token_admin;
        localStorage.setItem("token_admin", token);  //    localStorage
        setSuccessMessage("  !");  //    
        navigate('/admin'); //    
      } else {
        //   -     
        setError("   .");
      }
    } catch (error) {
      if (error.response) {
        //    (,    )
        if (error.response.status === 401) {
          setError("   .");
        } else {
          setError("   .");
        }
      } else {
        //    
        setError("   .");
      }
    }
  };

  return (
    <>
      <Header />
      <div className={styles.loginForm}>
        <p className={styles.loginForm_title}>  </p>

        {/*     */}
        {error && <div className={styles.errorMessage}>{error}</div>}

        {/*     */}
        {successMessage && <div className={styles.successMessage}>{successMessage}</div>}

        <form onSubmit={handleSubmit}>
          <div className={styles.inputGroup}>
            <input
              type="text"
              name="username"
              value={data.username}
              onChange={handleChange}
              placeholder=": "
              required
            />
          </div>
          <div className={styles.inputGroup}>
            <input
              type="password"
              name="password"
              value={data.password}
              onChange={handleChange}
              placeholder=": "
              required
            />
          </div>
          <div>
            <button type="submit" className={styles.submitButton}></button>
          </div>
        </form>

        {/*     */}
        <div className={styles.back}>
          <Link className={styles.backNav} to={"/"}> </Link>
        </div>
      </div>
      <div className={styles.light2}>
        <img src="/images/light2_image.png" alt="light2_image" />
      </div>
    </>
  );
}
