  import { useState, useEffect } from 'react';
  import axios from 'axios';
  import Skeleton from 'react-loading-skeleton';  // Import Skeleton
  import 'react-loading-skeleton/dist/skeleton.css'; // Import CSS
  import styles from './UserScores.module.css';
import { Link } from 'react-router-dom';

  export default function UserScores({ username }) {
    const [scores, setScores] = useState([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);

    const [uniqueCombinations, setUniqueCombinations] = useState(0);
    const [totalTests, setTotalTests] = useState(0);
    const [totalScore, setTotalScore] = useState(0);
    const [averageScore, setAverageScore] = useState(0);

    useEffect(() => {
      const fetchScores = async () => {
        try {
          const response = await axios.get('/api/userscores', {
            headers: {
              Authorization: `Bearer ${localStorage.getItem('token')}`,
            },
          });

          setScores(response.data);
          const totalPoints = response.data.reduce((acc, score) => acc + (score.score || 0), 0);
          const average = totalPoints / response.data.length;

          setTotalScore(totalPoints);
          setAverageScore(average);
          setTotalTests(response.data.length);

          const uniqueScores = new Set(
            response.data.map(score => `${score.class || ' '}-${score.subject || ' '}`)
          );
          setUniqueCombinations(uniqueScores.size);

        } catch (error) {
          setError('  ');
        } finally {
          setLoading(false);
        }
      };

      fetchScores();
    }, []);

    const LastScore = ({ scores }) => {
      const lastScore = scores[scores.length - 1];
      if (!lastScore) {
        return <p> .</p>;
      }
      return (
        <div className={styles.lastScore}>
          <p className={styles.lastScoreTitle}> :</p>
          <p>: <span className={styles.commonSpan}>{lastScore.score || '0'}  5</span></p>
          <p>: <span className={styles.commonSpan}>{lastScore.subject || ' '}</span></p>
          <p> : <span className={styles.commonSpan}>#{lastScore.documentNumber || ' '}</span></p>
          <p>: <span className={styles.commonSpan}>{lastScore.date || ' '}</span></p>
        </div>
      );
    };

    return (
      <div className={styles.userscores}>
        <p className={styles.title}>, {username}!</p>

        {error && <div style={{ color: 'red' }}>{error}</div>}

        {loading ? (
          <div className="skeleton-container">
                <Skeleton className="skeleton-item" height={40} width={400} />
                <Skeleton className="skeleton-item" height={40} width={400} />
                <Skeleton className="skeleton-item" height={40} width={200} />
                <Skeleton className="skeleton-item" height={40} width={500} />
                <Skeleton className="skeleton-item" height={40} count={8} />
        </div>
        ) : (
          <>
            <div className={styles.activity}>
              <p> (  ): <span className={styles.commonSpan}>{totalScore}</span></p>
            </div>
            <div className={styles.activity}>
              <p> : <span className={styles.commonSpan}>{averageScore.toFixed(2) || '0'}  5</span></p>
            </div>

            <LastScore scores={scores} />

            <div className={styles.activity}>
              <p>   ( + ): <span className={styles.commonSpan}>{uniqueCombinations}</span></p>
            </div>

            <div className={styles.activity}>
              <p>   : <span className={styles.commonSpan}>{totalTests}</span></p>
            </div>

            <div className={styles.userBlock}>
              <div className={styles.lastFive}>
                <Link to={'/userscores'}>  :</Link>
              </div>
              {scores.length > 0 ? (
                <div className={styles.userTableWrapper}>
                                <table className={styles.userTable}>
                  <thead>
                    <tr>
                      <th></th>
                      <th></th>
                      <th></th>
                      <th></th>
                      <th></th>
                      <th style={{ width: 40 }}></th>
                    </tr>
                  </thead>
                  <tbody>
                    {scores.slice(-5).reverse().map((score, index) => (
                      <tr key={index}>
                        <td>{index + 1}</td> {/*     5  */}
                        <td>{score.score || '0'}  5</td>
                        <td>{score.class || ' '}</td>
                        <td>#{score.documentNumber || ' '}</td>
                        <td>{score.subject || ' '}</td>
                        <td>{score.date || ' '}</td>
                      </tr>
                    ))}
                  </tbody>
                </table>
                </div>
              ) : (
                <p>   .</p>
              )}
            </div>
          </>
        )}
      </div>
    );
  }
