    import React, { useState, useEffect } from 'react';
    import { Link, useNavigate } from 'react-router-dom';
    import styles from './AdminHeader.module.css'; //    
    import axios from 'axios';

    export default function AdminHeader({ children }) {
      const navigate = useNavigate(); //  
      const [loading, setLoading] = useState(false); //   

      //     
      const token = localStorage.getItem('token_admin'); //    localStorage

      //   (logout)
      const handleLogout = async () => {
        if (!token) {
          alert(':   . ,  .');
          return;
        }

        setLoading(true);
        try {
          const response = await axios.post('/api/admin/logout', {}, {
            headers: {
              Authorization: `Bearer ${token}`,
            },
          });

          if (response.status === 200) {
            localStorage.removeItem('token_admin');
            navigate('/auth/admin');
          } else {
            alert('  .  .');
          }
        } catch (error) {
          console.error('  :', error);
          alert('  .  .');
        } finally {
          setLoading(false);
        }
      };

      return (
        <div className={styles.adminPanel}>
          <div className={styles.sidebar}>
            <div className={styles.logo}>
            <Link to={'/admin'}>
                        <img src="/images/erudit.svg" alt="erudit_logo" />
                    </Link>
            </div>
            <Link to="/admin/questions" className={styles.sidebarBtn}> </Link>
            <Link to="/admin/users" className={styles.sidebarBtn}></Link>
            <Link to="/admin/ratings" className={styles.sidebarBtn}> </Link>

            <div className={styles.user}>
                <Link to="/" className={styles.userLink}>  </Link>

            </div>

            {/*  Logout */}
            {token ? (
                <div className={styles.logBtn}>
                                <button
                className={styles.logoutBtn}
                onClick={handleLogout}
                disabled={loading}
              >
                {loading ? '...' : ''}
              </button>
                </div>
            ) : (
              <div className={styles.loginReminder}>
                <Link to="/auth/admin"> </Link>
              </div>
            )}
          </div>
          <div className={styles.content}>
              {children}
          </div>

        </div>
      );
    }
