import { useState } from "react";
import axios from "axios";
import { Link } from "react-router-dom";
import styles from './Register.module.css';  // Assuming you have a separate CSS module for the Register page
import Header from "../../../components/Header";

export default function Register() {
  const [formData, setFormData] = useState({
    username: '',
    firstname: '',
    lastname: '',
    thirdname: '',
    email: '',
    phone: '',
    organization: '',
    password: '',
    password_confirmation: '',
  });

  const [error, setError] = useState(null);  // For error messages
  const [successMessage, setSuccessMessage] = useState(null); // For success message
  const [isChecked, setIsChecked] = useState(false);  // For the checkbox

  // Handle input changes
  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({
      ...formData,
      [name]: value,
    });
  };

  // Handle checkbox change
  const handleCheckboxChange = (e) => {
    setIsChecked(e.target.checked);
  };

  // Handle form submission
  const handleSubmit = async (e) => {
    e.preventDefault();
    setError(null);
    setSuccessMessage(null);

    if (!isChecked) {
      setError("      .");
      return;  // Prevent form submission if not checked
    }

    try {
      // Sending data to the server
      const response = await axios.post("/api/register", formData);
      setSuccessMessage("  !");

      // Redirect to login after successful registration
      setTimeout(() => {
        window.location.href = '/auth/login';  // You can use navigate() if you prefer
      }, 2000);
    } catch (error) {
      if (error.response) {
        setError(error.response.data.message || "   .");
      } else {
        setError("   .");
      }
    }
  };

  return (
    <>
      <Header />
      <div className={styles.registerForm}>
        <p className={styles.registerForm_title}></p>

        {/* Show error message */}
        {error && <div className={styles.errorMessage}>{error}</div>}

        {/* Show success message */}
        {successMessage && <div className={styles.successMessage}>{successMessage}</div>}

        <form onSubmit={handleSubmit}>
          <div className={styles.inputGroup}>
            <input
              type="text"
              name="username"
              value={formData.username}
              onChange={handleChange}
              placeholder=" "
              required
            />
          </div>
          <div className={styles.inputGroup}>
            <input
              type="text"
              name="firstname"
              value={formData.firstname}
              onChange={handleChange}
              placeholder=""
              required
            />
          </div>
          <div className={styles.inputGroup}>
            <input
              type="text"
              name="lastname"
              value={formData.lastname}
              onChange={handleChange}
              placeholder=""
              required
            />
          </div>
          <div className={styles.inputGroup}>
            <input
              type="text"
              name="thirdname"
              value={formData.thirdname}
              onChange={handleChange}
              placeholder=" ()"
            />
          </div>
          <div className={styles.inputGroup}>
            <input
              type="email"
              name="email"
              value={formData.email}
              onChange={handleChange}
              placeholder=" "
              required
            />
          </div>
          <div className={styles.inputGroup}>
            <input
              type="text"
              name="phone"
              value={formData.phone}
              onChange={handleChange}
              placeholder=" ()"
            />
          </div>
          <div className={styles.inputGroup}>
            <input
              type="text"
              name="organization"
              value={formData.organization}
              onChange={handleChange}
              placeholder=" ()"
            />
          </div>
          <div className={styles.inputGroup}>
            <input
              type="password"
              name="password"
              value={formData.password}
              onChange={handleChange}
              placeholder=""
              required
            />
          </div>
          <div className={styles.inputGroup}>
            <input
              type="password"
              name="password_confirmation"
              value={formData.password_confirmation}
              onChange={handleChange}
              placeholder=" "
              required
            />
          </div>

            <div className={styles.checkbox}>
            <label>
              <input
                type="checkbox"
                checked={isChecked}
                onChange={handleCheckboxChange}
                />
                <Link to="/terms">  </Link>
            </label>

                </div>
          <div>
            <button type="submit" className={styles.submitButton}></button>
          </div>

          <div className={styles.reg_nav}>
            <p className={styles.reg_nav_text}>  ? <Link to={'/auth/login'}></Link></p>
          </div>
        </form>
      </div>
      <div className={styles.light2}>
        <img src="/images/light2_image.png" alt="light2_image" />
      </div>
    </>
  );
}
