import { Link, NavLink } from "react-router-dom";
import styles from './Header.module.css';
import { useNavigate } from 'react-router-dom';
import { useState, useEffect } from 'react'; // Import useEffect to manage token state
import axios from "axios";
import Footer from "./Footer/Footer";

export default function Header({ children, home }) {
    const navigate = useNavigate();
    const [loading, setLoading] = useState(false);
    const [token, setToken] = useState(null); // State for storing the token
    const [isMenuOpen, setIsMenuOpen] = useState(false); // State to toggle menu visibility

    // Fetch the token once when the component mounts
    useEffect(() => {
        const storedToken = localStorage.getItem('token');
        setToken(storedToken); // Store token in state
    }, []); // This will run only once after the initial render

    const handleLogout = async () => {
        setLoading(true);
        try {
            if (!token) {
                alert(':   . ,  .');
                return;
            }

            //      
            const response = await axios.post('/api/logout', {}, {
                headers: {
                    Authorization: `Bearer ${token}`,
                },
            });

            // Check the status code for success
            if (response.status === 200) {
                //   ,    localStorage
                localStorage.removeItem('token');
                
                // Set token to null in state to avoid it showing the logout button again
                setToken(null);

                //    
                navigate('/');
            } else {
                alert('  .  .');
            }
        } catch (error) {
            console.error('Logout error:', error);
            alert('  .  .');
        } finally {
            setLoading(false);
        }
    };

    const toggleMenu = () => {
        setIsMenuOpen(!isMenuOpen); // Toggle the menu state
    };

    return (
        <>


            <div className={styles.header}>
                                {/* - */}
                                <div className={styles.burger} onClick={toggleMenu}>
                    <span className={styles.bar}></span>
                    <span className={styles.bar}></span>
                    <span className={styles.bar}></span>
                </div>
                <div className={styles.logo}>
                    <Link to={'/'}>
                        <img src="/images/erudit.svg" alt="erudit_logo" />
                    </Link>
                </div>  
                {/*  */}
                <div className={`${styles.nav} ${isMenuOpen ? styles.open : ''}`}>
                    <NavLink 
                        to={'/quiz'} 
                        className={({ isActive }) => isActive ? `${styles.navLink} ${styles.active}` : styles.navLink}
                    >
                        
                    </NavLink>
                    <NavLink 
                        to={'/ratings'} 
                        className={({ isActive }) => isActive ? `${styles.navLink} ${styles.active}` : styles.navLink}
                    >
                        
                    </NavLink>

                    <NavLink 
                        to={'/check-document'} 
                        className={({ isActive }) => isActive ? `${styles.navLink} ${styles.active}` : styles.navLink}
                    >
                         
                    </NavLink>
                    <NavLink 
                        to={'/leaders'} 
                        className={({ isActive }) => isActive ? `${styles.navLink} ${styles.active}` : styles.navLink}
                    >
                        -100
                    </NavLink>

                    {/* Render the logout button only if there is a token in localStorage */}
                    {token ? (
                        <>
                            <NavLink 
                                to={'/profile'} 
                                className={({ isActive }) => isActive ? `${styles.navLink} ${styles.active}` : styles.navLink}
                            >
                                 
                            </NavLink>
                            <div className={styles.authButtons}>
                            <button 
                                className={styles.logoutButton} 
                                onClick={handleLogout} 
                                disabled={loading}
                            >
                                {loading ? '...' : ''}
                            </button>
                            </div>
                        </>
                    ) : (
                        <div className={styles.authButtons}>
                        
                            <NavLink 
                            to={'/auth/login'} 
                            className={({ isActive }) => isActive ? `${styles.loginButton} ${styles.active}` : styles.loginButton}
                        >
                            
                        </NavLink>
                        </div>

                    )}
                </div>


            </div>
            <div className={home ? styles.home : styles.content}>
                {children}

            </div>
            <div className={styles.iconImages}>
                <img src={"images/icon3.svg"} alt="" className={styles.icon1} />
                <img src={"images/icon3.svg"} alt="" className={styles.icon2} />

                </div>
            {/* <Footer/> */}
        </>
    );
}
