import { useState, useEffect, useCallback } from 'react';
import axios from 'axios';
import Header from '../../../components/Header';
import styles from './Profile.module.css';
import UserScores from '../Score/UserScores';
import Skeleton from 'react-loading-skeleton';
import 'react-loading-skeleton/dist/skeleton.css';

function Profile() {
  const [user, setUser] = useState(null);
  const [error, setError] = useState(null);
  const [successMessage, setSuccessMessage] = useState(null);
  const [formData, setFormData] = useState({
    firstname: '',
    lastname: '',
    thirdname: '',
    phone: '',
    organization: '',
  });
  const [passwordData, setPasswordData] = useState({
    oldPassword: '',
    newPassword: '',
    confirmPassword: '',
  });
  const [activeTab, setActiveTab] = useState('personal');
  const [loading, setLoading] = useState(true);

  const getAuthHeader = () => ({
    Authorization: `Bearer ${localStorage.getItem('token')}`,
  });

  // Fetch user data on mount
  const fetchUser = useCallback(async () => {
    try {
      const response = await axios.get('/api/user', {
        headers: getAuthHeader(),
      });
      setUser(response.data);
      setFormData({
        firstname: response.data.firstname,
        lastname: response.data.lastname,
        thirdname: response.data.thirdname || '',
        phone: response.data.phone || '',
        organization: response.data.organization || '',
      });
    } catch (error) {
      setError('   ');
    } finally {
      setLoading(false);
    }
  }, []);

  useEffect(() => {
    fetchUser();
  }, [fetchUser]);

  // Handle form changes (profile or password)
  const handleFormChange = (e) => {
    const { name, value } = e.target;
    setFormData((prev) => ({ ...prev, [name]: value }));
  };

  const handlePasswordChange = (e) => {
    const { name, value } = e.target;
    setPasswordData((prev) => ({ ...prev, [name]: value }));
  };

  // Update profile
  const handleUpdateProfile = async (e) => {
    e.preventDefault();
    setError(null);
    setSuccessMessage(null);

    try {
      await axios.put('/api/user/update', formData, {
        headers: getAuthHeader(),
      });
      setSuccessMessage('  !');
    } catch (error) {
      setError('   ');
    }
  };

  const handleChangePassword = async (e) => {
    e.preventDefault();
    setError(null);
    setSuccessMessage(null);
  
    // Check if the passwords match before sending the request
    if (passwordData.newPassword !== passwordData.confirmPassword) {
      setError('  ');
      return;
    }
  
    // Check password length locally before sending to backend
    if (passwordData.newPassword.length < 8) {
      setError('     8 ');
      return;
    }
  
    try {
  
      const response = await axios.put(
        '/api/user/change-password',
        {
          oldPassword: passwordData.oldPassword,
          newPassword: passwordData.newPassword,
          newPassword_confirmation: passwordData.confirmPassword, // Correct parameter name
        },
        {
          headers: getAuthHeader(),
        }
      );
  
      // Show success message if the password was successfully changed
      setSuccessMessage('  !');
      setPasswordData({
        oldPassword: '',
        newPassword: '',
        confirmPassword: '',
      }); // Clear password fields after success
  
    } catch (error) {
      // Show error message from the backend if there's an issue
      const errorMessage =
        error.response?.data?.error || '   ';
      setError(errorMessage);
    } finally {
      setLoading(false); // Set loading state to false after the request finishes
    }
  };
  



  return (
      <Header>
      <div className={styles.profileBlocksContainer}>
        <div className={styles.profileBlocks}>
          {loading ? (
            <div>
              <Skeleton height={40} width={600} />
              <Skeleton height={40} width={500} />
              <Skeleton height={40} width={650} />
              <Skeleton height={40} width={500} />
              <Skeleton height={40} count={10} />
            </div>
          ) : (
            <>
              <UserScores username={user ? user.username : ''} />
              <div className={styles.profileContainer}>
                {/* Tab navigation */}
                <div className={styles.tabs}>
                      <button
                        className={activeTab === 'personal' ? `${styles.activeTab}` : ''} // Apply activeTab styles if it's active
                        onClick={() => setActiveTab('personal')}
                      >
                         
                      </button>
                      <button
                        className={activeTab === 'security' ? `${styles.activeTab}` : ''} // Apply activeTab styles if it's active
                        onClick={() => setActiveTab('security')}
                      >
                        
                      </button>
                    </div>


                {/* Tab Content */}
                {activeTab === 'personal' && (
                  <div className={styles.personalTabContent}>
                    <p className={styles.profileHeader}> </p>
                    {user && (
                      <form className={styles.profileForm} onSubmit={handleUpdateProfile}>
                        <div className={styles.inputGroup}>
                          <label>:</label>
                          <input
                            type="text"
                            name="firstname"
                            value={formData.firstname}
                            onChange={handleFormChange}
                            placeholder=""
                            required
                          />
                        </div>
                        <div className={styles.inputGroup}>
                          <label>:</label>
                          <input
                            type="text"
                            name="lastname"
                            value={formData.lastname}
                            onChange={handleFormChange}
                            placeholder=""
                            required
                          />
                        </div>
                        <div className={styles.inputGroup}>
                          <label>:</label>
                          <input
                            type="text"
                            name="thirdname"
                            value={formData.thirdname}
                            onChange={handleFormChange}
                            placeholder=""
                          />
                        </div>
                        <div className={styles.inputGroup}>
                          <label>:</label>
                          <input
                            type="text"
                            name="phone"
                            value={formData.phone}
                            onChange={handleFormChange}
                            placeholder=""
                          />
                        </div>
                        <div className={styles.inputGroup}>
                          <label>:</label>
                          <input
                            type="text"
                            name="organization"
                            value={formData.organization}
                            onChange={handleFormChange}
                            placeholder=""
                          />
                        </div>
                        <button type="submit" className={styles.submitButton}>
                           
                        </button>
                        {successMessage && (
            <div className={styles.successMessage}>{successMessage}</div>
          )}
                       {error && (
                        <div className={styles.errorMessage}>{error}</div>
                      )}
                      </form>
                    )}
                  </div>
                )}

                {activeTab === 'security' && (
                  <div className={styles.securityTabContent}>
                    <p className={styles.changePasswordTitle}> </p>
                    <form onSubmit={handleChangePassword}>
                      <div className={styles.inputGroup}>
                        <label> :</label>
                        <input
                          type="password"
                          name="oldPassword"
                          value={passwordData.oldPassword}
                          onChange={handlePasswordChange}
                          placeholder=" "
                          required
                        />
                      </div>
                      <div className={styles.inputGroup}>
                        <label> :</label>
                        <input
                          type="password"
                          name="newPassword"
                          value={passwordData.newPassword}
                          onChange={handlePasswordChange}
                          placeholder=" "
                          required
                        />
                      </div>
                      <div className={styles.inputGroup}>
                        <label> :</label>
                        <input
                          type="password"
                          name="confirmPassword"
                          value={passwordData.confirmPassword}
                          onChange={handlePasswordChange}
                          placeholder="  "
                          required
                        />
                      </div>
                      <button type="submit" className={styles.submitButton}>
                         
                      </button>
                      {successMessage && (
                            <div className={styles.successMessage}>{successMessage}</div>
                        )}
                      {error && (
                        <div className={styles.errorMessage}>{error}</div>
                      )}
                    </form>

                  </div>
                )}
              </div>
            </>
          )}

        </div>
      </div>
      </Header>
  );
}

export default Profile;
