import React, { useState, useEffect } from 'react';
import axios from 'axios';
import AdminHeader from '../../components/AdminHeader/AdminHeader';
import styles from './AdminPanel.module.css'; //  

export default function AdminPanel() {
  const [scorersCount, setScorersCount] = useState(0);
  const [loading, setLoading] = useState(false); //   
  const [error, setError] = useState(null); //    

  //    
  const fetchScorersCount = async () => {
    const token = localStorage.getItem("token_admin"); //    localStorage
    if (!token) {
      setError("  ");
      return;
    }

    setLoading(true); //   
    try {
      const response = await axios.get('/api/admin/scorers-count', {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      });
      setScorersCount(response.data.total_scorers);
    } catch (error) {
      setError("   ");
      console.error(":", error);
    } finally {
      setLoading(false); //   
    }
  };

  useEffect(() => {
    fetchScorersCount();
  }, []);

  return (
      <AdminHeader>
              <div className={styles.content}>
                {/* ,      */}
                {loading && <p>...</p>}
                {error && <p className={styles.error}>{error}</p>}

                {/*    */}
                {!loading && !error && (
                  <div className={styles.scorersCount}>
                    <h2>  : {scorersCount}</h2>
                  </div>
                )}
      </div>
      </AdminHeader>
  );
}
